import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-okay-cookies',
  templateUrl: './okay-cookies.component.html',
  styleUrls: ['./okay-cookies.component.css']
})
export class OkayCookiesComponent implements OnInit {

  accept = 'no';
  seMostro = false;
  constructor() { }

  ngOnInit(): void {

    if (localStorage.getItem('cookie')) {
      this.accept = localStorage.getItem('cookie');
      console.log('Existe, no debemos mostrar');
      this.seMostro = true;
      return;
    }

    console.log('Debemos mostrar');



  }

  aceptar() {
    this.accept = 'si';
    localStorage.setItem('cookie', this.accept);
    this.seMostro = false;
    console.log('Se aceptó, debería eliminar el banner');
    const divCookie = document.querySelector('#div-cookies')
    divCookie.remove()
  }
  cancelar() {
    this.accept = 'no';
    localStorage.setItem('cookie', this.accept);
    this.seMostro = false;
    console.log('Se denegó, debería eliminar el banner');
    const divCookie = document.querySelector('#div-cookies')
    divCookie.remove()
  }

}
