import { NgModule } from '@angular/core';
import { CommonModule, } from '@angular/common';
import { BrowserModule  } from '@angular/platform-browser';
import { Routes, RouterModule } from '@angular/router';

import { ProfileComponent } from './examples/profile/profile.component';
import { LandingComponent } from './examples/landing/landing.component';
import { NucleoiconsComponent } from './components/nucleoicons/nucleoicons.component';
import {ComponentsComponent} from './components/components.component';
import {ContactComponent} from './examples/contact/contact.component';
import {AboutComponent} from './examples/about/about.component';
import {TerminosComponent} from './examples/terminos/terminos.component';
import {CookiesComponent} from './examples/cookies/cookies.component';
import {LegalComponent} from './examples/legal/legal.component';
import {AltaPropietarioComponent} from './examples/alta-propietario/alta-propietario.component';
import {PrivacidadComponent} from './examples/privacidad/privacidad.component';

const routes: Routes = [
    { path: '', redirectTo: 'inicio', pathMatch: 'full' },
    {path: 'helpers', component: ComponentsComponent},
    { path: 'contacto',             component: ContactComponent },
    { path: 'sobre-nosotros',             component: AboutComponent },
    { path: 'inicio',             component: LandingComponent },
    { path: 'user-profile',     component: ProfileComponent },
    { path: 'terminos-condiciones',           component: TerminosComponent },
    { path: 'nucleoicons',      component: NucleoiconsComponent },
    { path: 'politicas-cookies',           component: CookiesComponent },
    { path: 'aviso-legal',           component: LegalComponent },
    { path: 'alta-propietario',           component: AltaPropietarioComponent },
    { path: 'politica-privacidad',           component:  PrivacidadComponent},



];

@NgModule({
  imports: [
    CommonModule,
    BrowserModule,
    RouterModule.forRoot(routes)
  ],
  exports: [
  ],
})
export class AppRoutingModule { }
